import _get from 'lodash.get';
import { convertTimezoneToLocal } from '../../views/application/property/components/Calendar/helpers';
import { TBooking, TAppointment } from '../../types/YouCanBook';

const getEmail = (booking: TBooking): string => {
	const emailField = _get(booking, ['answers'], []).filter(
		answer => answer.code === 'EMAIL'
	)[0];

	return _get(emailField, 'string', '');
};

const filterCancelledBookings = (booking: TBooking): boolean =>
	!booking.cancelled;

export const remapBooking = (booking: TBooking): TAppointment => {
	const start = _get(booking, 'startsAt');
	const end = _get(booking, 'endsAt');
	const timeZone = _get(booking, 'timeZone');

	return {
		title: getEmail(booking),
		id: _get(booking, 'id'),
		startDate: convertTimezoneToLocal(start, timeZone),
		endDate: convertTimezoneToLocal(end, timeZone),
		services: _get(booking, 'services', []),
		answers: _get(booking, 'answers', []),
		cancelled: _get(booking, 'cancelled'),
	};
};

export const remapBookings = (bookings: Array<TBooking>): Array<TAppointment> =>
	bookings.filter(filterCancelledBookings).map(booking => remapBooking(booking));
