import React, { useState } from 'react';
import { Tab, Tabs } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import { makeStyles } from '@material-ui/styles';
import _get from 'lodash.get';
import { gql, useQuery } from '@apollo/client';
import PropertyEdit from '../../application/property/PropertyEdit';
import MainCard from '../../../ui-component/cards/MainCard';
import { TabPanel } from '../../application/components/TabPanel';
import Calendar from '../../application/property/components/Calendar';
import graphqlSchema from '../../../graphql';
import config from '../../../config';

const useStyles = makeStyles({
	toolbar: {
		'&&': {
			padding: 0,
		},
	},
	tab: {
		textTransform: 'none',
	},
});

const TABS = {
	edit: 'edit',
	calendar: 'calendar',
};

const EditPage = () => {
	const { id } = useParams<{ id: string }>();
	const classes = useStyles();
	const [tab, setTab] = useState(TABS.edit);

	const editPropertyProps = useQuery(gql(graphqlSchema.getProperty), {
		variables: { PropertyId: id },
	});

	const handleChange = (event: React.ChangeEvent<{}>, newValue: string) => {
		setTab(newValue);
	};

	const property = _get(editPropertyProps, 'data.getProperty') || {};
	const calendarProfileId = _get(property, 'HomeViewingCalendarProfileId');

	return (
		<>
			<MainCard title='' sx={{ mb: 3 }} contentClass={classes.toolbar}>
				<Tabs value={tab} onChange={handleChange}>
					<Tab value={TABS.edit} label='Edit' classes={{ root: classes.tab }} />
					{calendarProfileId && config.enableCalendar && (
						<Tab
							value={TABS.calendar}
							label='Calendar'
							classes={{ root: classes.tab }}
						/>
					)}
				</Tabs>
			</MainCard>
			<TabPanel value={tab} index={TABS.edit}>
				<PropertyEdit {...editPropertyProps} />
			</TabPanel>
			<TabPanel value={tab} index={TABS.calendar}>
				{calendarProfileId && <Calendar property={property} />}
			</TabPanel>
		</>
	);
};

export default EditPage;
